import React, { Component } from 'react';
import _ from 'lodash';
import Radium from 'radium';
import './App.css';
import Person from './Person/Person';
import ErrorBoundary from './ErrorBoundary/ErrorBoundary';

const Styles = {
  button : {
    marginLeft:'10px',
    width: '150px',
    height: 'auto',
    border: 'solid 1px grey',
    borderRadius:'2px',
    padding:'5px',
    ':hover': {
      backgroundColor:'green',
      border: 'solid 1px orange',
      color:'white'
    }
  },
  personContainer: {
      width:'100%',
      height:'auto',
      display:'inline-block',
      textAlign: 'left',
      marginLeft:'10px'
  }
}

class App extends Component {
  state = {
    person : [
      {id: "1AA", name: "Roger", age:36 },
      {id: "1AB", name: "Sachin", age:44 },
      {id: "1AC", name: "Sindhu", age:25 },
    ]
  }

  addValueHandler() {
    const name = document.querySelector('#new_name').value;
    const age = document.querySelector('#age').value;

    console.log('Add Value', name, age);

    if (name && age) {
      const currentPersonState = [...this.state.person];

      const newPerson = {id:Math.floor(Math.random() * 11), name: name, age: age};
      currentPersonState.push(newPerson);
      console.log(currentPersonState);

      this.setState({
        person : currentPersonState
      });
    } else {
      console.log("Please enter the values");
    }
  }

  personDeleteHandler(personId) {
    const currentPersonState = [...this.state.person];
    const personIndex = _.findIndex(currentPersonState, {'id': personId});

    if(personIndex !== -1) {
       this.setState({
        person : currentPersonState.splice(personIndex, 1)
      })
    }
  }

  render() {
    const personList = this.state.person.map((person, index) => {
      if(person.name === 'Sachin')  {
        return <ErrorBoundary  key={person.id + '-' +index}> <Person
                  name={person.name} 
                  age = {person.age} 
                  onClick = {this.personDeleteHandler.bind(this, person.id)}
                >
                  <p><span className="hobbyLabel">Hobbies Are:</span> 
                  <span className="hobby">Football, Travelling</span></p>
              </Person></ErrorBoundary>
      } else {
        return  <ErrorBoundary  key={person.id + '-' +index}><Person 
                  name={person.name} 
                  age = {person.age} 
                  onClick = {this.personDeleteHandler.bind(this, person.id)}
              /> </ErrorBoundary>
      }
    });

    return (
      <div className="App">
       <h3>T App</h3>

       <hr/>
       <input type="text" id="new_name" placeholder="Name..."/> 
       <input type="number" id="age" placeholder="Age..." style={{marginLeft:'10px'}} min="1" max="90"/>
       <input type="button" style={Styles.button} value="Add Value" onClick={this.addValueHandler.bind(this)} />
       
       <hr/>
       <div style={Styles.personContainer}>
          {personList}
      </div>
      </div>
    );
  }
}

export default Radium(App);



